//=require components/menu.js

$(document).ready(function () {
    
    // Slider
    
    $('#slider').slick({
        prevArrow: '<i class="prev-arrow slick-arrow"></i> ',
        nextArrow: '<i class="next-arrow slick-arrow"></i> ',
        autoplay: true
    });


    $(window).on('resize', function (e) {
        var init = $(".card-box").data('init-slider');
        if (window.innerWidth < 482) {
            if (init != 1) {
                $('#card-box').slick({
                    prevArrow: '<i class="prev-arrow slick-arrow"></i> ',
                    nextArrow: '<i class="next-arrow slick-arrow"></i> ',
                    infinite: true,
                    slidesToShow: 1,
                    slidesToScroll: 1
                }).data({
                    'init-slider': 1
                });
            }
        }
        else {
            if (init == 1) {
                $('#card-box').slick('unslick').data({
                    'init-slider': 0
                });
            }
        }
    }).trigger('resize');

    $('.top-nav').mouseenter(function () {
        $('.slider').css({'zIndex' : '-1'})
    });
    $('.top-nav').mouseleave(function () {
        $('.slider').css({'zIndex' : '0'})
    });

    // Cards

    $('.card-box .card').mouseenter(function () {
        $(this).find('.card-text').show();
    })
    $('.card-box .card').mouseleave(function () {
        $(this).find('.card-text').hide();
    })

    // Spoiler

    $('.spoiler').click(function () {
        $(this).prev().slideToggle();
        $(this).toggleClass('close');
        if ($(this).text() === 'Развернуть') {
            $(this).text('Свернуть');
        } else {
            $(this).text('Развернуть');
        }
    })
    
    // Modal
    $('.callme-btn').click(function () {
        $('.overlay').fadeIn(500);
        $('.recall').slideDown(700);
    });
    $('.get-me-price').click(function () {
        $('.overlay').fadeIn(500);
        $('.get-price').slideDown(700);
    });
    $('.overlay').click(function () {
       $('.modal').slideUp(700);
       $(this).fadeOut(500);
    })
    $('.modal .close').click(function () {
        $('.modal').slideUp(700);
        $('.overlay').fadeOut(500);
    })

    // Mobile menu
    $('.mob-menu').click(function () {
        $(this).toggleClass('open');
        if ($(this).hasClass('open')) {
            $('#nav').slideDown(300);
        } else {
            $('#nav').slideUp(300);
        }
    })

    $('.sub > a').click(function name(event) {
        event.preventDefault();
    })

});

var inputs = document.querySelectorAll( '.inputfile' );
Array.prototype.forEach.call( inputs, function( input )
{
	var label	 = input.nextElementSibling,
		labelVal = label.innerHTML;

	input.addEventListener( 'change', function( e )
	{
		var fileName = '';
		if( this.files && this.files.length > 1 )
			fileName = ( this.getAttribute( 'data-multiple-caption' ) || '' ).replace( '{count}', this.files.length );
		else
			fileName = e.target.value.split( '\\' ).pop();

		if( fileName )
			label.querySelector( 'span' ).innerHTML = fileName;
		else
			label.innerHTML = labelVal;
	});
});